import { cloneDeep } from 'lodash';
import { focusNextInput, focusPreviousInput } from './utils';

const callChange = (event, handlers, isArrowChange) => {
  if (event.target.name === 'day') handlers.onDayChange(event, isArrowChange);
  if (event.target.name === 'month')
    handlers.onMonthChange(event, isArrowChange);
  if (event.target.name === 'year') handlers.onYearChange(event, isArrowChange);
};

const setTarget = (event, v) => {
  const n = cloneDeep(event);
  let newValue = parseInt(n.target.value || 0, 10) + v;
  if (newValue < 1) newValue = '01';
  if (event.target.name === 'day' && newValue > 31) newValue = 31;
  if (event.target.name === 'month' && newValue > 12) newValue = 12;
  n.target.value = newValue;
  return n;
};

// eslint-disable-next-line max-statements, complexity
export const onKeyDownHelper = (event, handlers) => {
  switch (event.key) {
    case 'ArrowLeft': {
      event.preventDefault();
      focusPreviousInput(event.target);
      break;
    }
    case 'ArrowUp': {
      event.preventDefault();
      const n = setTarget(event, 1);
      callChange(n, handlers, true);
      break;
    }
    case 'ArrowDown': {
      event.preventDefault();
      const n = setTarget(event, -1);
      callChange(n, handlers, true);
      break;
    }
    case 'ArrowRight': {
      event.preventDefault();
      focusNextInput(event.target);
      break;
    }
    case 'Tab': {
      if (event.shiftKey) break;
      if (event.target.value.length > 0 && event.target.name !== 'year')
        event.preventDefault();
      callChange(event, handlers);
      break;
    }
    case 'Backspace': {
      // HTML Date input behavior, clear everything when deleting
      event.preventDefault();
      const n = cloneDeep(event);
      n.target.value = '';
      callChange(n, handlers, true);
      break;
    }
    case 'Enter': {
      break;
    }
    default:
      break;
  }
};
