import React, { useState, useEffect } from 'react';
import moment from 'moment';
import {
  commonInputProps,
  triggerOnBlur,
  focusNextInput,
  getFormattedDay,
  getShouldShortcircuitDay,
  getFormattedMonth,
  getShouldShortcircuitMonth,
  getFormattedYear,
} from './utils';
import { onKeyDownHelper } from './helpers';
import {
  DateInputDivider,
  DateInputWrapper,
  DateInputInput,
  DateInputFluidContainer,
} from './styled';
import { DateInputsPropTypes } from './propsTypes';

const FORMAT = 'MMDDYYYY';

export const DateInputs = ({
  onBlur,
  onChange,
  onDateChange,
  disabled,
  onCustomKeyDown,
  time,
  tabIndex,
  innerRef,
}) => {
  const [month, setMonth] = useState('');
  const [day, setDay] = useState('');
  const [year, setYear] = useState('');
  const [onChangeDateTrigger, setOnChangeDateTrigger] = useState({});
  const dayRef = React.useRef<HTMLInputElement>();
  const [
    haveInputsOnChangeTriggered,
    setHaveInputsOnChangeTriggered,
  ] = useState(false);

  let digits = 2;
  let placeholder = '';

  const handleFluidClick = React.useCallback((e: React.MouseEvent<Element, MouseEvent>) => {
    if(!(e.target as HTMLInputElement).type && dayRef.current) {
      dayRef.current.focus();
    }
  }, [dayRef.current])
  
  const triggerOnDateChange = React.useCallback(() => {
    if (month && day && year && haveInputsOnChangeTriggered) {
      const momentValue = moment(`${month}${day}${year}`, 'MMDDYYYY', true);
      if (momentValue.isValid()) {
        onDateChange(momentValue);
      }
      setHaveInputsOnChangeTriggered(false);
    }
  }, [day, month, year, haveInputsOnChangeTriggered]);

  // isArrowChange is coming from the onKeyDown handler in helpers
  const onMonthChange = React.useCallback((e, isArrowChange) => {
    const value = getFormattedMonth(e.target.value);
    const shouldAdvance = getShouldShortcircuitMonth(e.target.value);
    setMonth(value);
    setHaveInputsOnChangeTriggered(true);
    if (e.target.value.length > 1 || shouldAdvance) {
      if (!isArrowChange) focusNextInput(e.target);
      setOnChangeDateTrigger({});
    }
  },[]);

  // isArrowChange is coming from the onKeyDown handler in helpers
  const onDayChange = React.useCallback((e, isArrowChange) => {
    const value = getFormattedDay(e.target.value);
    const shouldAdvance = getShouldShortcircuitDay(e.target.value);
    setDay(value);
    setHaveInputsOnChangeTriggered(true);
    if (e.target.value.length > 1 || shouldAdvance) {
      if (!isArrowChange) focusNextInput(e.target);
      setOnChangeDateTrigger({});
    }
  },[]);

  const onYearChange = React.useCallback((e) => {
    const value = getFormattedYear(e.target.value);
    setYear(value);
    setHaveInputsOnChangeTriggered(true);
    setOnChangeDateTrigger({});
  },[]);

  useEffect(() => {
    // When component mounts haveInputsOnChangeTriggered is set to false
    // so this is not triggered on mount but is triggered with all other changes
    if (haveInputsOnChangeTriggered) {
      const momentValue = moment(`${month}${day}${year}`, FORMAT, true);
      if (month || day || year)
        onChange(
          `${month}${day}${year}`,
          momentValue.isValid() ? momentValue : null,
        );
    }
  }, [day, month, year]);

  useEffect(() => {
    triggerOnDateChange();
  }, [onChangeDateTrigger]);

  useEffect(() => {
    if (
      time &&
      time !== `${month}${day}${year}` &&
      moment(time, 'MMDDYYYY').isValid()
    ) {
      const values = String(time || '');
      if (values.slice(0, 2)) setMonth(values.slice(0, 2));
      if (values.slice(2, 4)) setDay(values.slice(2, 4));
      if (values.slice(4, 8)) setYear(values.slice(4, 8));
    } else if (!time && (month || day || year)) {
      setMonth('');
      setDay('');
      setYear('');
    }
  }, [time]);

  const onKeyDown = (event) =>
    onKeyDownHelper(event, {
      onDayChange,
      onMonthChange,
      onYearChange,
    });

  const renderMonthInput = () => {
    placeholder = 'MM';
    const onMonthInputBlur = (e) => {
      setOnChangeDateTrigger({});
      triggerOnBlur(e, onBlur);
    };
    return (
      <DateInputInput
        {...commonInputProps(digits, onKeyDown, placeholder)}
        autoComplete="off"
        key="month-input"
        data-testid="month"
        disabled={disabled}
        name="month"
        onBlur={onMonthInputBlur}
        onChange={onMonthChange}
        value={month}
        tabIndex={tabIndex}
        ref={dayRef}
      />
    );
  };
  const renderDayInput = () => {
    placeholder = 'DD';
    const onDayInputBlur = (e) => {
      setOnChangeDateTrigger({});
      triggerOnBlur(e, onBlur);
    };
    return (
      <DateInputInput
        {...commonInputProps(digits, onKeyDown, placeholder)}
        autoComplete="off"
        key="day-input"
        data-testid="day"
        disabled={disabled}
        name="day"
        onBlur={onDayInputBlur}
        onChange={onDayChange}
        value={day}
        tabIndex={tabIndex}
      />
    );
  };
  const renderYearInput = () => {
    placeholder = 'YY';
    digits = 4;
    const onYearInputBlur = (e) => {
      triggerOnBlur(e, onBlur);
    };
    return (
      <DateInputInput
        {...commonInputProps(digits, onKeyDown, placeholder)}
        autoComplete="off"
        key="year-input"
        data-testid="year"
        disabled={disabled}
        name="year"
        onBlur={onYearInputBlur}
        onChange={onYearChange}
        value={year}
        size="l"
        tabIndex={tabIndex}
      />
    );
  };

  const renderDateInputs = () => {
    const inputs = [renderMonthInput(), renderDayInput(), renderYearInput()];
    const inputsWithDividers = inputs.reduce(
      (acc, input, index) =>
        input
          ? acc.concat([
              index ? <DateInputDivider>/</DateInputDivider> : null,
              input,
            ])
          : acc,
      [],
    );

    return [
      inputsWithDividers,
      React.createElement('span', { key: 'span' }), // hack for next/prev focus
    ];
  };

  return (
    <DateInputFluidContainer
      onClick={handleFluidClick}
      data-testid="date-input-fluidContainer"
    >
    <DateInputWrapper
      disabled={disabled}
      ref={innerRef}
      onKeyDown={onCustomKeyDown}
      tabIndex={-1}
      cols={['auto', '6px', 'auto', '6px', 'auto']}
    >
      {renderDateInputs()}
    </DateInputWrapper>
    </DateInputFluidContainer>
  );
};

DateInputs.propTypes = DateInputsPropTypes;
