import React from 'react';
import { describe } from 'react-desc';
import DSInput from '../../Input';
import { DateInputs } from './DateInputs';
import { props, defaultProps } from './props';

const DSDateInputV2 = ({
  onFocus,
  onBlur,
  onChange,
  onDateChange,
  onKeyDown,
  onError,
  value,
  disabled,
  containerProps,
  tabIndex,
  innerRef,
}) => (
  <DSInput
    innerRef={innerRef}
    containerProps={containerProps}
    customInputType={({ innerRef: ir }) => (
      <DateInputs
        disabled={disabled}
        innerRef={ir}
        onBlur={onBlur}
        onChange={onChange}
        onDateChange={onDateChange}
        onCustomKeyDown={onKeyDown}
        onError={onError}
        time={value}
        tabIndex={tabIndex}
      />
    )}
    disableTooltip={false}
    disabled={disabled}
    onBlur={onBlur}
    onFocus={onFocus}
    onKeyDown={onKeyDown}
    value={value}
    tabIndex={tabIndex}
  />
);

DSDateInputV2.defaultProps = defaultProps;

DSDateInputV2.propTypes = props;

const DSDateInputV2WithSchema = describe(DSDateInputV2);
DSDateInputV2WithSchema.propTypes = props;

export { DSDateInputV2, DSDateInputV2WithSchema };
