/* eslint-disable max-statements */
/* eslint-disable complexity */
/* eslint-disable no-underscore-dangle */
/* eslint-disable react/state-in-constructor */
/* eslint-disable no-restricted-globals */
/* eslint-disable react/prop-types */
/* eslint-disable max-lines */
import React from 'react';
import moment from 'moment';
import DSInputGroup from '@elliemae/ds-basic/form/InputGroup';
import { DSDateInputV2 } from '@elliemae/ds-basic/form/DateInputV2';
import DSDatePickerController from './DatePickerController/DatePickerController';
import DatePickerDropdown from './DatePickerDropdown';

const FORMAT = 'MMDDYYYY';
export default class DSdatePickerImpl extends React.Component {
  constructor(props) {
    super(props);
    this.pickerRef = React.createRef();
    this.state = {
      dataInput: '',
      selectedDate: this.getSafeValue(),
      inputFocused: false,
    };
  }

  // don't remove this because the component explodes if you do
  state = {};

  static getDerivedStateFromProps(nextProps, prevState) {
    const { dataInput: prevVal, selectedDate: prevDate } = prevState || {};
    const { value: nextVal, date: nextDate } = nextProps;

    const isPrevValMoment = prevVal instanceof moment;
    const isPrevDateMoment = prevDate instanceof moment;
    const prevValAsString = isPrevValMoment ? prevVal.format(FORMAT) : prevVal;

    // if users wants to clear, we use '==' to catch both undefined AND null
    if (nextVal == null && nextDate == null) {
      return {
        dataInput: '',
        selectedDate: null,
      };
    }

    // when val is an incomplete state, we don't want to mess with the state
    //  it's incomplete when length is not the full mmddyyyy (length === 8)
    if (prevValAsString?.length < 8) {
      return null;
    }

    const prevDateAsString = isPrevDateMoment
      ? prevDate.format(FORMAT)
      : prevDate;

    const isNextValMoment = nextVal instanceof moment;
    const isNextDateMoment = nextDate instanceof moment;
    const nextValAsString = isNextValMoment ? nextVal.format(FORMAT) : nextVal;
    const nextValAsMoment = isNextValMoment ? nextVal : moment(nextVal);
    const nextDateAsString = isNextDateMoment
      ? nextDate.format(FORMAT)
      : nextDate;
    const nextDateAsMoment = isNextDateMoment ? nextDate : moment(nextDate);

    const hasValChanged = nextValAsString !== prevValAsString;
    const hasDateChanged = nextDateAsString !== prevDateAsString;

    const newState = {};
    let skipStateChange = true;

    // value has priority over date, if both date and value change we take date
    // as such, order of condition is meaningful
    if (hasDateChanged) {
      skipStateChange = false;
      newState.dataInput = nextDateAsString;
      newState.selectedDate = nextDateAsMoment;
    }

    if (hasValChanged) {
      skipStateChange = false;
      newState.dataInput = nextValAsString;
      newState.selectedDate = nextValAsMoment;
    }

    if (skipStateChange) return null;
    return newState;
  }

  onClickPicker = () => {
    this.setState({ inputFocused: true });
  };

  getSafeValue = () => {
    const { value, date, defaultValue } = this.props;
    const { selectedDate } = this.state;
    if (value) {
      return moment(value, FORMAT, true);
    }
    if (date) {
      return moment(date, FORMAT, true);
    }
    if (defaultValue) {
      return moment(defaultValue, FORMAT, true);
    }
    if (selectedDate) {
      return selectedDate;
    }
    return null;
  };

  getSafeInputValue = () => {
    const safeValue = this.getSafeValue();
    if (safeValue && safeValue.isValid()) {
      return safeValue.format(FORMAT);
    }
    const { dataInput } = this.state;
    return dataInput;
  };

  isControlled = () => {
    const { value, date } = this.props;
    return Boolean(value) || Boolean(date);
  };

  handleDateChange = (date, event) => {
    const {
      readOnly,
      onDateChange,
      keepOpenOnDateSelect,
      onChange,
      onBlur,
    } = this.props;

    if (!readOnly) {
      if (!this.isControlled()) {
        this.setState({ dataInput: date.format(FORMAT), selectedDate: date });
      }
      onChange(date.format(FORMAT), date);
      onDateChange(date);
      if (!keepOpenOnDateSelect || event.key === 'Enter') {
        this.setState({ inputFocused: false });
        onBlur(date.format(FORMAT), date);
      }
    }
  };

  handleInputChange = (dataInput, newDate) => {
    const { onChange } = this.props;
    const newState = {
      dataInput,
      selectedDate: newDate,
    };
    this.setState(() => newState);
    onChange(dataInput, newDate);
  };

  handleChange = (newDate) => {
    if (!newDate) return;
    const { onDateChange } = this.props;
    const momentValue = moment(newDate, FORMAT, true);
    if (!momentValue.isValid()) return;
    this.setState({ selectedDate: momentValue });

    onDateChange(momentValue);
  };

  handleFocus = ({ focused }) => {
    this.setState({ inputFocused: focused });
  };

  handleCalendarClose = () => {
    const { onClose } = this.props;

    this.setState({ inputFocused: false });
    this.handleOnBlur();
    onClose();
  };

  handleClickEscape = () => {
    const { onClose } = this.props;

    this.setState({ inputFocused: false });
    this.handleOnBlur();
    onClose();
  };

  handleOnBlur = () => {
    setTimeout(() => {
      const { onBlur, autoClear } = this.props;
      const { dataInput, selectedDate } = this.state;

      if (moment(selectedDate, FORMAT, true).isValid()) {
        onBlur(dataInput, selectedDate);
      } else {
        if (autoClear) {
          this.setState({ dataInput: undefined });
        }
        onBlur(dataInput, null);
      }
    });
  };

  handleInputFocus = (dataInput) => {
    const { onFocus } = this.props;
    onFocus(dataInput);
  };

  render() {
    const { inputFocused } = this.state;
    const {
      disabled,
      numberOfMonths,
      readOnly,
      enableOutsideDays,
      transitionDuration,
      firstDayOfWeek,
      keepOpenOnDateSelect,
      hideKeyboardShortcutsPanel,
      onPrevMonthClick,
      onNextMonthClick,
      onClose,
      phrases,
      placeholder,
      displayFormatDay,
      isDayBlocked,
      isOutsideRange,
      isDayHighlighted,
      tabIndex,
      zIndex,
      pickerProps,
      calendarProps,
    } = this.props;
    const safedate = this.getSafeValue();
    const safeInputValue = this.getSafeInputValue();
    const hasSelectedDate = !!safedate;
    return (
      <DSInputGroup
        rightAddon={
          <DatePickerDropdown
            pickerRef={this.pickerRef}
            disabled={disabled}
            isOpen={inputFocused}
            menu={
              <div
                style={{ minHeight: '310px' }}
                className={
                  hasSelectedDate
                    ? 'has-selected-date'
                    : 'without-selected-date'
                }
                data-testid="date-picker-portal-wrapper"
              >
                <DSDatePickerController
                  date={safedate}
                  displayFormatDay={displayFormatDay}
                  enableOutsideDays={enableOutsideDays}
                  firstDayOfWeek={firstDayOfWeek}
                  hideKeyboardShortcutsPanel={hideKeyboardShortcutsPanel}
                  isDayBlocked={isDayBlocked}
                  isDayHighlighted={isDayHighlighted}
                  isOutsideRange={isOutsideRange}
                  keepOpenOnDateSelect={keepOpenOnDateSelect}
                  numberOfMonths={numberOfMonths}
                  onClickEscape={this.handleClickEscape}
                  onClose={(...args) => {
                    if (this.pickerRef.current) this.pickerRef.current.focus();
                    onClose(...args);
                    this.setState({ inputFocused: false });
                  }}
                  onDateChange={this.handleDateChange}
                  onFocusChange={this.handleFocus}
                  onNextMonthClick={onNextMonthClick}
                  onOutsideClick={this.handleCalendarClose}
                  onPrevMonthClick={onPrevMonthClick}
                  phrases={phrases}
                  transitionDuration={transitionDuration}
                  calendarProps={calendarProps}
                />
              </div>
            }
            onClick={this.onClickPicker}
            readOnly={readOnly}
            zIndex={zIndex}
            pickerProps={pickerProps}
          />
        }
      >
        <DSDateInputV2
          data-testid="datepicker-input"
          aria-label={placeholder || 'Date Picker Input'}
          disabled={disabled}
          onBlur={this.handleOnBlur}
          onChange={this.handleInputChange}
          onDateChange={this.handleChange}
          onFocus={this.handleInputFocus}
          placeholder={placeholder}
          tabIndex={tabIndex}
          value={safeInputValue}
        />
      </DSInputGroup>
    );
  }
}
