import React from 'react';
import PropTypes from 'prop-types';
import { convertPropToCssClassName } from '@elliemae/ds-classnames';
import DSPopper from '@elliemae/ds-basic/Popper';
import DatePickerPicker, {
  PickerPropsTypes,
} from './DatePickerPicker/DatePickerPicker';
const { classNameElement } = convertPropToCssClassName('datepicker-range');

const noop = () => null;
function DatePickerDropdown({
  disabled = false,
  readOnly,
  onClick = noop,
  isOpen,
  menu,
  zIndex,
  pickerProps,
  pickerRef,
}) {
  return (
    <DSPopper
      contentComponent={menu}
      isOpen={isOpen}
      modifiers={{
        preventOverflow: {
          enabled: true,
          padding: 0,
          boundariesElement: 'window',
        },
      }}
      showArrow={false}
      triggerComponent={
        <div>
          <DatePickerPicker
            innerRef={pickerRef}
            {...pickerProps}
            className={classNameElement('picker')}
            disabled={disabled}
            onClick={onClick}
            readOnly={readOnly}
          />
        </div>
      }
      zIndex={zIndex}
    />
  );
}

DatePickerDropdown.propTypes = {
  pickerRef: PropTypes.oneOfType([
    PropTypes.func,
    PropTypes.shape({ current: PropTypes.any }),
  ]),
  disabled: PropTypes.bool,
  readOnly: PropTypes.bool,
  onClick: PropTypes.func,
  isOpen: PropTypes.bool,
  menu: PropTypes.element,
  zIndex: PropTypes.number,
  pickerProps: PropTypes.shape(PickerPropsTypes),
};

export default DatePickerDropdown;
